<template>
	<view>
		<view class="inputBox">
			<view class="total">可提现金额：￥256</view>
			<uni-easyinput placeholder="请输入提现金额" v-model="money"></uni-easyinput>
			<view class="btn" @click="tixian">确定</view>
		</view>
		<view class="records-title">
			提现记录
		</view>
		<view class="records">
			<view class="item">
				<view class="title">金额</view>
				<view class="time">2021-12-12</view>
			</view>
			<view class="item">
				<view class="title">金额</view>
				<view class="time">2021-12-12</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				money: ''
			}
		},
		onLoad() {

		},
		onShow() {


		},
		onUnload() {

		},
		methods: {
			tixian() {
				if (this.$u.test.empty(this.money)) {
					this.$u.toast('提现金额为空')
					return
				}
				if(!this.$u.test.number(this.money)){
					this.$u.toast('金额类型不对')
					return
				}
			}
		}
	}
</script>

<style scoped>
